}


.flex{
    display: flex;
}
.flex-col{
    flex-direction: column;
}
.justify-center{
    justify-content: center;
}
.justify-between{
    justify-content: space-between;
}
.justify-start{
    justify-content: flex-start;
}

.justify-end{
    justify-content: flex-end;
}
.items-center{
    align-items: center;
}
.item-start{
    align-items: flex-start;
}
.item-end{
    align-items: flex-end;
}

